<script>
  import Logo from '@/components/Logo.vue'
  import NavLink from '@/components/NavLink.vue'
  import IsMobileMixin from '@/mixins/IsMobileMixin.js'

  export default {
    name: 'Header',
    data: function() {
      return {
        menuOpen: false,
        mobile: false
      }
    },
    components: {
      Logo,
      NavLink
    },
    mixins: [
      IsMobileMixin
    ],
    methods: {
      handleMenu: function () {
        if (!this.menuOpen) {
          this.menuOpen = true
        } else {
          this.menuOpen = false
        }
      }
    },
    created() {
      if (this.isMobile()) {
       this.mobile = true;
      }
    },
    watch: {
      $route() {
        this.menuOpen = false
      }
    }
  }
</script>

